"use client"
import { useState, useEffect } from "react"
import { ModalWrapper } from "@/components/ui/modal-wrapper"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useToast } from "@/hooks/use-toast"
import { useStore } from "@/store"
import axios from "@/utils/axiosConfig"

interface CreateNotificationModalProps {
  onClose: () => void
}

export function CreateNotificationModal({ onClose }: CreateNotificationModalProps) {
  const { user } = useStore.getState().profile
  const { toast } = useToast()

  const [formData, setFormData] = useState({
    message: "",
    targetType: "all"
  })

  const [selectedUserId, setSelectedUserId] = useState<number | null>(null)
  const [isSubmitting, setIsSubmitting] = useState(false)

  const availableTargets = user?.role === "ROLE_ADMIN" ? ["all", "specific"] : ["all", "specific"]

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setFormData({ ...formData, message: e.target.value })
  }

  const handleSelectChange = (value: string) => {
    setFormData({ ...formData, targetType: value })
    if (value !== "specific") {
      setSelectedUserId(null)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!user) return
    setIsSubmitting(true)
    try {
      const token = localStorage.getItem("token")
      if (user.role === "ROLE_ADMIN") {
        await axios.post("/notification/notifications/staff", { message: formData.message }, {
          headers: { Authorization: `Bearer ${token}` },
        })
      } else {
        if (formData.targetType === "specific" && selectedUserId) {
          await axios.post("/notification/notifications", 
            { userId: selectedUserId, message: formData.message },
            { headers: { Authorization: `Bearer ${token}` } }
          )
        } else {
          await axios.post("/notification/notifications", 
            { message: formData.message },
            { headers: { Authorization: `Bearer ${token}` } }
          )
        }
      }
      toast({
        title: "Success",
        description: "Notification sent successfully",
      })
      setFormData({ message: "", targetType: "all" })
      setSelectedUserId(null)
      onClose()
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to send notification",
        variant: "destructive",
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <ModalWrapper
      title="Create Notification"
      description={`Send a notification to ${user?.role === "ROLE_ADMIN" ? "staff" : "customers"}`}
      isOpen={true}
      onClose={onClose}
    >
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="message">Message</Label>
          <Textarea
            id="message"
            name="message"
            rows={4}
            value={formData.message}
            onChange={handleChange}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="targetType">Send To</Label>
          <Select value={formData.targetType} onValueChange={handleSelectChange}>
            <SelectTrigger>
              <SelectValue placeholder="Select recipients" />
            </SelectTrigger>
            <SelectContent>
              {availableTargets.map((t) => (
                <SelectItem key={t} value={t}>
                  {t === "all" ? "All Users" : "Specific User"}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {formData.targetType === "specific" && (
          <div className="space-y-2">
            <Label>Select User</Label>
            <input
              type="number"
              placeholder="Enter user ID"
              value={selectedUserId ?? ""}
              onChange={(e) => setSelectedUserId(Number(e.target.value))}
              className="w-full rounded-md border p-2"
            />
          </div>
        )}

        <div className="flex justify-end space-x-2 pt-4">
          <Button type="button" variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button type="submit" disabled={isSubmitting || (formData.targetType === "specific" && !selectedUserId)}>
            {isSubmitting ? "Sending..." : "Send Notification"}
          </Button>
        </div>
      </form>
    </ModalWrapper>
  )
}
